.main-reply-comment {
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  .image-comment-time-name {
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    grid-gap: 15px;
    img {
      height: 40px;
      width: 40px;
      border-radius: 50%;
    }

    .name-time-comment {
      display: flex;
      flex-direction: column;
      .name-time {
        display: flex;
        grid-gap: 5px;
        text-align: center;
        h4 {
          font-size: 18px;
          font-weight: 600;
          color: #404040;
        }
        span {
          font-size: 14px;
          color: #404040;
          display: flex;
          align-items: center;
        }
      }
      p {
        font-size: 15px;
        font-weight: 500;
        margin-top: 5px;
        color: #404040;
      }
      .replay_btn {
        button {
          margin-top: 10px;
          color: #fff;
          background-color: #42e619;
          border-color: #34e724;
          display: flex;
          justify-content: center;
          align-items: center;
          font-weight: 400;
          cursor: pointer;
          border: 1px solid transparent;
          padding: 2px 6px;
          font-size: 13px;
          border-radius: 5px;
          transition: all 0.5s;
        }
      }
      .reply_box.show {
        display: flex;
      }
      .reply_box {
        display: none;
        //display: flex;
        flex-direction: column;
        grid-gap: 12px;
        .image-input {
          margin-top: 8px;
          display: flex;
          justify-content: start;
          grid-gap: 10px;
          img {
            width: 30px;
            height: 30px;
            border-radius: 50%;
          }
          input {
            width: 100%;
            outline: none;
            border: none;
            border-bottom: 1px solid gray;
          }
        }
        .reply_submit {
          display: flex;
          justify-content: flex-end;
          grid-gap: 8px;
          button.cancle {
            background-color: rgb(233, 75, 75);
          }
          .submit,
          .cancle {
            color: #fff;
            background-color: #fa5c7c;
            display: flex;
            justify-content: center;
            align-items: center;
            font-weight: 400;
            cursor: pointer;
            border: 1px solid transparent;
            padding: 5px 10px;
            font-size: 13px;
            border-radius: 5px;
          }
        }
      }
      .reply_comment {
        margin-top: 15px;
        display: flex;
        justify-content: space-around;
        .reply_comment_image_name_time {
          cursor: pointer;
          display: flex;
          justify-content: space-between;
          grid-gap: 15px;
          img {
            width: 30px;
            height: 30px;
            border-radius: 50%;
          }
        }
      }
    }
  }
  .action {
    cursor: pointer;
  }
}

.comment_submit {
  margin-top: 30px;
  h2 {
    color: #404040;
  }
  .form-group {
    .form-control {
      height: 130px;
      padding: 15px;
    }
    .btn {
      background-color: #e6c719;
      color: #fff;
    }
  }
  .login-first {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 10px;
    margin-top: 10px;
    li {
      transform: all 0.5s;
      span {
        display: flex;
        color: #fff;
      }
      button,
      a {
        color: #fff;
        background-color: transparent;
      }
      &:nth-child(1) {
        background-color: #2926dd;
        border-color: #4826dd;
        &:hover {
          background-color: #2715c7;
        }
      }
      &:nth-child(2) {
        background-color: #fa5c7c;
        border-color: #fa5c7c;
        &:hover {
          background-color: #e64968;
        }
      }
      &:nth-child(3) {
        background-color: #e65b1b;
        border-color: #8862e0;
        &:hover {
          background-color: #db5517;
        }
      }
    }
  }
}
